<template>
  <el-form ref="form" label-width="100px" :model="myForm" :inline="myInline">
    <el-form-item
      v-for="item in myFormLabel"
      :key="item.label"
      :label="item.label"
    >
      <el-input
        v-if="item.type === 'input'"
        :placeholder="'请输入' + item.label"
        v-model="myForm[item.model]"
      ></el-input>
      <el-switch
        v-if="item.type === 'switch'"
        v-model="myForm[item.model]"
      ></el-switch>
      <el-date-picker
        v-if="item.type === 'date'"
        type="date"
        value-format="yy-MM-dd"
        placeholder="选择日期"
        v-model="myForm[item.model]"
      ></el-date-picker>
      <el-select
        v-if="item.type === 'select'"
        placeholder="请选择"
        v-model="myForm[item.model]"
      >
        <el-option
          v-for="subItem in item.opts"
          :key="subItem.value"
          :label="subItem.label"
          :value="subItem.value"
        ></el-option>
      </el-select>
    </el-form-item>
    <el-form-item><slot></slot></el-form-item>
  </el-form>
</template>
<script>
export default {
  name: "CommonForm",
  //报错：vue.runtime.esm.js?c320:4560 [Vue warn]: Avoid mutating a prop directly since the value
  // will be overwritten whenever the parent component re-renders. Instead, use a data or computed property
  // based on the prop's value. Prop being mutated: "placement"
  // 原因：elementUi升级到2.15.6以上版本的时候在date-picker组件里面添加了一行直接修改placement的代码
  // 解决：降低element ui的版本就行
  props: {
    formLabel: Array,
    form: Object,
    inline: Boolean,
  },
  // data() {
  //   return {
  //     myFormLabel: this.formLabel,
  //     myForm: this.form,
  //     myInline: this.inline,
  //   };
  // },

  computed: {
    myFormLabel: {
      get() {
        return this.formLabel;
      },
      set(val) {
        this.formLabel = val;
        return this.formLabel;
      },
    },
    myForm: {
      get() {
        return this.form;
      },
      set(val) {
        this.form = val;
      },
    },
    myInline: {
      get() {
        return this.inline;
      },
      set(val) {
        this.inline = val;
        return this.inline;
      },
    },
  },
};
</script>
