<template>
  <div class="common_table">
    <el-table :data="myTableData" style="height: 90%" stripe>
      <el-table-column
        v-for="item in myTableLabel"
        :key="item.prop"
        :label="item.label"
        :width="item.width ? item.width : 125"
      >
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row[item.prop] }}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" min-width="180">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.row)">编辑</el-button>
          <el-button size="mini" type="danger" @click="handleDelete(scope.row)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      class="pager"
      layout="prev,pager,next"
      :total="myConfig.total"
      :current-page.sync="myConfig.page"
      @current-change="changePage"
      :page-size="20"
    >
    </el-pagination>
  </div>
</template>
<script>
export default {
  name: "CommonTable",
  props: {
    tableData: Array,
    tableLabel: Array,
    config: Object,
  },
  computed: {
    myTableData: {
      get() {
        return this.tableData;
      },
      set(val) {
        this.tableData = val;
        return this.tableData;
      },
    },
    myTableLabel: {
      get() {
        return this.tableLabel;
      },
      set(val) {
        this.tableLabel = val;
        return this.tableLabel;
      },
    },
    myConfig: {
      get() {
        return this.config;
      },
      set(val) {
        this.config = val;
        return this.config;
      },
    },
  },
  methods: {
    handleEdit(row) {
      this.$emit("edit", row);
    },
    handleDelete(row) {
      this.$emit("del", row);
    },
    changePage(page) {
      this.$emit("changePage", page);
    },
  },
};
</script>
<style lang="less" scoped>
.common_table {
  height: calc(100% - 62px);
  background-color: #fff;
  position: relative;
  .pager {
    position: absolute;
    bottom: 0;
    right: 20px;
  }
}
</style>
